import React from 'react';
import classes from './PosterControl.css';
import Button from '../../UI/Button/Button';

const posterControl = (props) => (
        <div className={classes.PosterControl}>
            <p>标题</p>
            <input type="text" onChange={props.titleEdit}/>
            <p>副标题</p> 
            <input type="text" onChange={props.subTitleEdit}/>
            <p>英文标题</p> 
            <input type="text" onChange={props.enTitleEdit}/>
            <p>主题颜色</p> 
            <input type="color" onChange={props.colorEdit}/>
            <p>二维码网址</p> 
            <input type="text" onChange={props.qcodeEdit}/>
            <Button 
            clicked={props.clicked}>导出pdf</Button>
        </div>  
);

export default posterControl;


